/* app/cadastro/cadastro.module.css */


.main {
 min-height: 80vh;
 padding: 40px 20px;
 /* Compensação para o footer fixo */
 padding-bottom: 100px;
 background-color: #f4f4f4;
 display: flex;
 flex-direction: column;
 align-items: center;
}


.card {
 background-color: white;
 padding: 2rem;
 border-radius: 8px;
 box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
 width: 100%;
 max-width: 600px;
}


.tituloPagina {
 color: #333;
 text-align: center;
 margin-bottom: 1.5rem;
 border-bottom: 2px solid #005F41;
 padding-bottom: 10px;
}


.form {
 display: flex;
 flex-direction: column;
 gap: 15px;
}


.inputGroup {
 display: flex;
 flex-direction: column;
}


.label {
 font-weight: bold;
 margin-bottom: 5px;
 color: #555;
}


.input, .textarea {
 padding: 10px;
 border: 1px solid #ccc;
 border-radius: 4px;
 font-size: 1rem;
}


.button {
 margin-top: 10px;
 padding: 12px;
 background-color: #005F41;
 color: white;
 border: none;
 border-radius: 4px;
 font-size: 1.1rem;
 font-weight: bold;
 cursor: pointer;
 transition: background-color 0.3s;
}


.button:hover {
 background-color: #00442e;
}
